<script>
	import {api} from '@/api/index.js'
	import {saveLog} from '@/utils/log.js'
	import Vue from 'vue'
	export default {
		data() {
			return {
				jpushModule: null,
				registrationID: '',
				connectStatus: 'Êú™ËøûÊé•',
			};
		},
		methods: {
			/** ÂàùÂßãÂåñÊûÅÂÖâÊé®ÈÄÅ */
			initJPush() {
				try {
					saveLog('ÂºÄÂßãÂàùÂßãÂåñ JPush...');
					this.jpushModule = uni.requireNativePlugin('JG-JPush');
					if (!this.jpushModule) {
						saveLog('[ÈîôËØØ] JPush ÂàùÂßãÂåñÂ§±Ë¥•ÔºöÊó†Ê≥ïËé∑ÂèñÊèí‰ª∂ÂÆû‰æã„ÄÇ');
						return;
					}
					this.jpushModule.initJPushService();
					saveLog('‚úÖ JPush ÊúçÂä°Â∑≤ÂàùÂßãÂåñ„ÄÇ');

					this.jpushModule.addConnectEventListener(result => {
						this.connectStatus = result.connectEnable ? 'Â∑≤ËøûÊé•' : 'Êú™ËøûÊé•';
						saveLog(`üì° JPush ËøûÊé•Áä∂ÊÄÅ: ${this.connectStatus}`);
						if (this.connectStatus === 'Â∑≤ËøûÊé•') {
							this.getRegistrationID();
						}
					});

					this.jpushModule.addNotificationListener(result => {
						saveLog('üì• Êî∂Âà∞ JPush ÈÄöÁü•:', result);
						// this.playLocalRingtone();
					});

					this.checkNotificationPermission();
				} catch (e) {
					saveLog(`[ÈîôËØØ] JPush ÂàùÂßãÂåñÂºÇÂ∏∏: ${e.message}`);
				}
			},

			/** ÂàõÂª∫Ëá™ÂÆö‰πâÈìÉÂ£∞Ê∏†ÈÅì */
			createCustomRingtoneChannel() {
				try {
					saveLog("üöÄ [ÈìÉÂ£∞Ê∏†ÈÅì] ÂáΩÊï∞Â∑≤ÊâßË°å");

					const main = plus.android.runtimeMainActivity();
					const AndroidVersion = plus.android.importClass('android.os.Build');

					if (AndroidVersion.SDK_INT < 26) {
						saveLog("‚ö†Ô∏è [ÈìÉÂ£∞Ê∏†ÈÅì] Android < 8.0Ôºå‰∏çÂàõÂª∫Ê∏†ÈÅì");
						return;
					}

					const NotificationManager = plus.android.importClass('android.app.NotificationManager');
					const NotificationChannel = plus.android.importClass('android.app.NotificationChannel');
					const Uri = plus.android.importClass('android.net.Uri');
					const AudioAttributes = plus.android.importClass('android.media.AudioAttributes');
					const RingtoneManager = plus.android.importClass('android.media.RingtoneManager');

					const manager = main.getSystemService(main.NOTIFICATION_SERVICE);
					const packageName = main.getPackageName();
					const res = main.getResources();

					// ‚ö† ÊØèÊ¨°ÂçáÁ∫ßÈÉΩÂøÖÈ°ªÊç¢Êñ∞ IDÔºåÂê¶Âàô‰∏ç‰ºöÊõ¥Êñ∞ÈìÉÂ£∞
					const channelId = "custom_ringtone";
					const channelName = "ËÆ¢ÂçïËØ≠Èü≥ÈÄöÁü•";
					const channelDesc = "Êí≠ÊîæËá™ÂÆö‰πâÈÄöÁü•ÈìÉÂ£∞";

					// ‚ö† ÂøÖÈ°ª‰øùËØÅÔºöraw/custom_ringtone.mp3 Â≠òÂú®
					const ringtoneResId = res.getIdentifier("custom_ringtone", "raw", packageName);
					saveLog("üéµ [ÈìÉÂ£∞Ê∏†ÈÅì] ÈìÉÂ£∞ËµÑÊ∫ê ID = " + ringtoneResId);

					if (ringtoneResId === 0) {
						saveLog("‚ùå [ÈìÉÂ£∞Ê∏†ÈÅì] Êâæ‰∏çÂà∞Ëá™ÂÆö‰πâÈìÉÂ£∞ÔºåËØ∑Ê£ÄÊü•Ë∑ØÂæÑÔºönativeResources/android/res/raw/custom_ringtone.mp3");
						return;
					}

					const ringtoneUri = Uri.parse(`android.resource://${packageName}/${ringtoneResId}`);
					saveLog("üîó [ÈìÉÂ£∞Ê∏†ÈÅì] ÈìÉÂ£∞ URI = " + ringtoneUri.toString());

					try {
						const ringtone = RingtoneManager.getRingtone(main, ringtoneUri);
						saveLog("üéß [ÈìÉÂ£∞Ê∏†ÈÅì] Á≥ªÁªüËß£ÊûêÈìÉÂ£∞ÊàêÂäü: " + ringtone.getTitle(main));
					} catch (e) {
						saveLog("‚ö†Ô∏è [ÈìÉÂ£∞Ê∏†ÈÅì] Á≥ªÁªüËß£ÊûêÈìÉÂ£∞Â§±Ë¥•: " + e.message);
					}

					let channel = manager.getNotificationChannel(channelId);

					if (!channel) {
						saveLog("üì¢ [ÈìÉÂ£∞Ê∏†ÈÅì] Ê∏†ÈÅì‰∏çÂ≠òÂú® ‚Üí ÂºÄÂßãÂàõÂª∫");

						const audioAttr = new AudioAttributes.Builder()
							.setContentType(AudioAttributes.CONTENT_TYPE_SONIFICATION)
							.setUsage(AudioAttributes.USAGE_NOTIFICATION)
							.build();

						channel = new NotificationChannel(channelId, channelName, NotificationManager.IMPORTANCE_HIGH);
						channel.setDescription(channelDesc);
						channel.enableLights(true);
						channel.enableVibration(true);
						channel.setSound(ringtoneUri, audioAttr);

						manager.createNotificationChannel(channel);
						saveLog("‚úÖ [ÈìÉÂ£∞Ê∏†ÈÅì] Ê∏†ÈÅìÂàõÂª∫ÂÆåÊàê");
					} else {
						saveLog("‚ö†Ô∏è [ÈìÉÂ£∞Ê∏†ÈÅì] Ê∏†ÈÅìÂ∑≤Â≠òÂú®Ôºà‰∏ç‰ºöÊõ¥Êñ∞ÈìÉÂ£∞Ôºâ„ÄÇÂ¶ÇÈúÄÊõ¥Êñ∞ÔºöÂç∏ËΩΩÈáçË£Ö App");
					}

					const created = manager.getNotificationChannel(channelId);
					saveLog("üîç [ÈìÉÂ£∞Ê∏†ÈÅì] ÊúÄÁªàÊ∏†ÈÅìÈìÉÂ£∞ = " + created.getSound());
					saveLog("üîç [ÈìÉÂ£∞Ê∏†ÈÅì] ÈáçË¶ÅÊÄß = " + created.getImportance());

				} catch (e) {
					saveLog("‚ùå [ÈìÉÂ£∞Ê∏†ÈÅì] ÂºÇÂ∏∏Ôºö" + e.message);
				}
			},

			/** Ëé∑Âèñ RegistrationID */
			getRegistrationID() {
				if (!this.jpushModule) return;
				this.jpushModule.getRegistrationID(result => {
					if (result.registerID) {
						this.registrationID = result.registerID;
						uni.setStorageSync('registerID', this.registrationID);
						saveLog(`‚úÖ Ëé∑Âèñ RegistrationID ÊàêÂäü: ${this.registrationID}`);
					} else {
						saveLog(`[ÈîôËØØ] Ëé∑Âèñ RegistrationID Â§±Ë¥•: ${result.errMsg}`);
					}
				});
			},

			/** Ê£ÄÊü•ÈÄöÁü•ÊùÉÈôê */
			checkNotificationPermission() {
				const main = plus.android.runtimeMainActivity();
				const NotificationManager = plus.android.importClass('android.app.NotificationManager');
				const nm = main.getSystemService(main.NOTIFICATION_SERVICE);
				if (plus.android.invoke(nm, 'areNotificationsEnabled')) {
					saveLog('üîî ÈÄöÁü•ÊùÉÈôêÂ∑≤ÂºÄÂêØ');
				} else {
					saveLog('‚ö†Ô∏è ÈÄöÁü•ÊùÉÈôêÊú™ÂºÄÂêØ');
					this.noticMsgTool();
				}
			},

			/** ÊèêÁ§∫Áî®Êà∑ÂºÄÂêØÈÄöÁü• */
			noticMsgTool() {
				uni.showModal({
					title: 'ÈÄöÁü•ÊùÉÈôêÊèêÈÜí',
					content: 'ÊÇ®ËøòÊ≤°ÊúâÂºÄÂêØÈÄöÁü•ÊùÉÈôêÔºåÊó†Ê≥ïÊé•Êî∂Ê∂àÊÅØÔºåËØ∑ÂâçÂæÄËÆæÁΩÆÔºÅ',
					showCancel: false,
					confirmText: 'ÂéªËÆæÁΩÆ',
					success: res => {
						if (res.confirm) {
							this.openNotificationSettings();
						}
					},
				});
			},

			/** ÊâìÂºÄÁ≥ªÁªüÈÄöÁü•ËÆæÁΩÆ */
			openNotificationSettings() {
				const main = plus.android.runtimeMainActivity();
				const Intent = plus.android.importClass('android.content.Intent');
				const Build = plus.android.importClass('android.os.Build');
				const Settings = plus.android.importClass('android.provider.Settings');
				const Uri = plus.android.importClass('android.net.Uri');
				try {
					const intent = new Intent();
					if (Build.VERSION.SDK_INT >= 26) {
						intent.setAction(Settings.ACTION_APP_NOTIFICATION_SETTINGS);
						intent.putExtra(Settings.EXTRA_APP_PACKAGE, main.getPackageName());
					} else if (Build.VERSION.SDK_INT >= 21) {
						intent.setAction(Settings.ACTION_APP_NOTIFICATION_SETTINGS);
						intent.putExtra("app_package", main.getPackageName());
						intent.putExtra("app_uid", main.getApplicationInfo().uid);
					} else {
						intent.setAction(Settings.ACTION_APPLICATION_DETAILS_SETTINGS);
						intent.setData(Uri.parse('package:' + main.getPackageName()));
					}
					main.startActivity(intent);
				} catch (e) {
					saveLog(`[ÈîôËØØ] ÊâìÂºÄÈÄöÁü•ËÆæÁΩÆÂ§±Ë¥•: ${e.message}`);
					const intent = new Intent(Settings.ACTION_SETTINGS);
					main.startActivity(intent);
				}
			},
		},
		onLaunch() {
			// uni.showToast({
			// 	title:'2.3.0'
			// })
			uni.$globalMethods = {
			    getRegistrationID: () => this.getRegistrationID()
			}
			try {
				const main = plus.android.runtimeMainActivity();
				const context = main;
				plus.android.importClass(context);

				const resources = context.getResources();
				plus.android.importClass(resources);

				const packageName = context.getPackageName();

				const resId = plus.android.invoke(
					resources,
					'getIdentifier',
					'custom_ringtone',
					'raw',
					packageName
				);

				saveLog('ÈìÉÂ£∞ËµÑÊ∫ê ID = ' + resId);

				if (!resId) {
					saveLog('‚ùå ËµÑÊ∫ê‰∏çÂ≠òÂú®ÔºåËØ∑Ê£ÄÊü•ÊòØÂê¶Ê≠£Á°ÆÊîæÂÖ• raw ÁõÆÂΩï');
				}
			} catch (e) {
				saveLog('‚ùå ÈîôËØØÔºö' + e);
			}

			// #ifdef APP-PLUS
			this.initJPush();
			this.createCustomRingtoneChannel();
			// #endif
		},


		onShow() {
			// #ifdef APP-PLUS
			if (this.jpushModule && !this.registrationID) {
				this.getRegistrationID();
			}
			// #endif
			// ÂºÄÂêØÁ∫ø‰∏äË∞ÉËØï
			// uni.setEnableDebug({
			//     enableDebug: false
			// })

			const options = {
				title: 'ÂºÄÂêØÊ∂àÊÅØÈÄöÁü•',
				content: 'ÂºÄÂêØÂêéÂèØÂèäÊó∂Êî∂Âà∞ÈáçË¶ÅÈÄöÁü•ÔºåÊòØÂê¶ÂâçÂæÄËÆæÁΩÆÂºÄÂêØÔºü',
				confirmText: 'ÂéªËÆæÁΩÆ',
				cancelText: 'ÊöÇ‰∏çÂºÄÂêØ',
				successToast: 'ÈÄöÁü•ÊùÉÈôêÂ∑≤ÂºÄÂêØÔºåÊÑüË∞¢ÊîØÊåÅÔºÅ',
				failToast: 'ÊÇ®Êú™ÂºÄÂêØÈÄöÁü•ÊùÉÈôêÔºåÂèØËÉΩÈîôËøáÈáçË¶ÅÊ∂àÊÅØ',
				successCallback: () => {},
				failCallback: () => {}
			}

			uni.getSetting({
				withSubscriptions: true,
				success: (res) => {
					if (!res.subscriptionsSetting.mainSwitch) {
						// ÊòæÁ§∫ËÆ¢ÈòÖÊ∂àÊÅØÂºïÂØºÂºπÁ™ó
						uni.showModal({
							title: options.title,
							content: options.content,
							confirmText: options.confirmText,
							cancelText: options.cancelText,
							success: (res) => {
								if (res.confirm) {
									uni.openSetting({
										success: (settingRes) => {
											if (uni.getSystemInfoSync()
												.platform === 'wechat') {
												if (settingRes.authSetting[
														'scope.subscribeMessage'
													]) {
													uni.showToast({
														title: options
															.successToast,
														icon: 'success'
													})
													options.successCallback()
												} else {
													uni.showToast({
														title: options
															.failToast,
														icon: 'none'
													})
													options.failCallback()
												}
											}
										},
										fail: (err) => {
											console.error('ÊâìÂºÄËÆæÁΩÆÂ§±Ë¥•', err)
											options.failCallback()
										}
									})
								} else {
									uni.showToast({
										title: 'ÊÇ®ÂèØ‰ª•ÈöèÊó∂Âú®ËÆæÁΩÆ‰∏≠ÂºÄÂêØÈÄöÁü•',
										icon: 'none'
									})
									options.failCallback()
								}
							},
							fail: (err) => {
								console.error('ÊòæÁ§∫ÂºπÁ™óÂ§±Ë¥•', err)
								options.failCallback()
							}
						})


					} else {

					}
				},
				fail: (err) => {
					console.error('Ëé∑ÂèñËÆæÁΩÆÂ§±Ë¥•', err)
				}
			})



			const updateManager = uni.getUpdateManager()
			// ËØ∑Ê±ÇÂÆåÊñ∞ÁâàÊú¨‰ø°ÊÅØÁöÑÂõûË∞É
			updateManager.onCheckForUpdate(res => {
				if (res.hasUpdate) {
					// Êñ∞ÁâàÊú¨‰∏ãËΩΩÊàêÂäü
					updateManager.onUpdateReady(() => {
						uni.showModal({
							title: 'Êõ¥Êñ∞ÊèêÁ§∫',
							content: 'Êñ∞ÁâàÊú¨Â∑≤ÁªèÂáÜÂ§áÂ•ΩÔºåÁÇπÂáªÁ°ÆÂÆöÈáçÂêØÂ∞èÁ®ãÂ∫è',
							success(res) {
								if (res.confirm) {
									// Êñ∞ÁöÑÁâàÊú¨Â∑≤Áªè‰∏ãËΩΩÂ•ΩÔºåÂº∫Âà∂Êõ¥Êñ∞
									updateManager.applyUpdate()
								}
							}
						})
					})
				}
			})
			// Êñ∞ÁâàÊú¨‰∏ãËΩΩÂ§±Ë¥•
			updateManager.onUpdateFailed(res => {
				console.error(res)
			})


		},



		onHide: function() {
			console.log('App Hide')
		}
	}
</script>

<style lang="scss">
	.icon {
		width: 1em;
		height: 1em;
		vertical-align: -0.15em;
		fill: currentColor;
		overflow: hidden;
	}

	/*ÊØè‰∏™È°µÈù¢ÂÖ¨ÂÖ±css */
	@import '@/uni_modules/uni-scss/index.scss';
	/* #ifndef APP-NVUE */
	@import '@/static/customicons.css';
	// ËÆæÁΩÆÊï¥‰∏™È°πÁõÆÁöÑËÉåÊôØËâ≤
	@import "@/static/iconfont.css";



	page {
		background-color: #f5f5f5;
	}

	/* #endif */
	.example-info {
		font-size: 14px;
		color: #333;
		padding: 10px;
	}

	// ÈöêËóèÊªöÂä®Êù°
	::-webkit-scrollbar {
		width: 0 !important;
	}

	::-webkit-scrollbar {
		width: 0 !important;
		height: 0;
	}


	.icon {
		width: 1em;
		height: 1em;
		vertical-align: -0.15em;
		fill: currentColor;
		overflow: hidden;
	}
</style>